<template>
  <v-container class="pa-2" fluid>
    <h1 class="display-1 font-weight-thin mb-4">
      Launch a New Service
    </h1>
    <v-divider />
    <v-stepper class="mt-4" v-model="step">
      <v-stepper-header>
        <v-stepper-step color="green" :complete="step > 1" step="1">
          Service Details
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step color="green" :complete="step > 2" step="2">
          Additional Settings
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step color="green" step="3">
          Finish
        </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card>
            <v-card-title>
              Basic Service Information
            </v-card-title>
            <v-card-subtitle>
              This information is visible to anyone who can connect to the service
            </v-card-subtitle>
            <v-card-text>
              <v-form>
                <v-row>
                  <v-col cols="12" md="6">
                    <v-text-field v-model="service.name"
                      autocomplete="off"
                      @input="SetServiceIdentifier(true)"
                      outlined
                      :rules="[rules.name]"
                      label="Service Name*"
                      required />
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="6">
                    <v-text-field v-model="serviceData.identifierSuffix"
                      @input="SetServiceIdentifier"
                      outlined
                      v-if="account"
                      :rules="[rules.identifier]"
                      :error-messages="serviceData.identifierError"
                      label="Service ID*"
                      required >
                      <template v-slot:prepend>
                        <span class="white--text">@{{ account.username }}</span>
                        <v-icon>mdi-slash-forward</v-icon>
                      </template>
                    </v-text-field>
                  </v-col>
                  <v-col cols="6" md="2">
                    <v-select
                      v-model="service.repository.provider"
                      :items="serviceData.repoProviders"
                      item-text="title"
                      item-value="value"
                      :append-outer-icon="serviceData.repoProviders.find(
                        v => v.value === service.repository.provider
                      ).icon || 'mdi-code-braces'"
                      label="Provider"
                      outlined />
                  </v-col>
                  <v-col cols="6" md="4">
                    <v-text-field v-model="service.repository.url"
                      outlined
                      label="Repository URL" />
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="6">
                    <v-col cols="12">
                      <v-select
                        v-model="serviceData.descriptionSource"
                        :items="['Text','URL']"
                        label="Description Source" />
                    </v-col>
                    <v-col v-if="serviceData.descriptionSource === 'Text'" cols="12">
                      <v-textarea
                        outlined
                        height="250"
                        label="Description"
                        v-model="service.meta.description.text" />
                    </v-col>
                    <v-col v-if="serviceData.descriptionSource === 'URL'" cols="12">
                      <v-text-field
                        outlined
                        v-model="service.meta.description.url"
                        label="Readme URL" />
                    </v-col>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-col cols="12">
                      <v-select
                        v-model="service.meta.license.type"
                        :items="serviceData.licenseTypes"
                        label="Select License" />
                    </v-col>
                    <v-col cols="12">
                      <v-select
                        v-model="serviceData.licenseSource"
                        :items="['Text','URL']"
                        label="License Source" />
                    </v-col>
                    <v-col v-if="serviceData.licenseSource === 'Text'" cols="12">
                      <v-textarea
                        outlined
                        label="License Text"
                        v-model="service.meta.license.text" />
                    </v-col>
                    <v-col v-if="serviceData.licenseSource === 'URL'" cols="12">
                      <v-text-field
                        outlined
                        v-model="service.meta.license.url"
                        label="License URL" />
                    </v-col>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
            <v-divider />
            <v-card-actions>
              <v-btn color="blue" @click="step = 2">
                continue
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-stepper-content>
        <v-stepper-content step="2">
          <v-card>
            <v-card-title>
              Service Settings
            </v-card-title>
            <v-card-subtitle>
              Customize the service features/settings as per your requirement.
            </v-card-subtitle>
            <v-card-text>
              <v-form>
                <v-row>
                  <v-col cols="12" md="6">
                    <v-checkbox
                      v-model="service.testing.enabled"
                      color="primary"
                      label="Enable Testing" />
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-text-field
                      outlined
                      v-if="service.testing.enabled"
                      v-model="service.testing.procedure"
                      label="Test Procedure" />
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="6">
                    <v-select
                      outlined
                      v-model="service.scope"
                      :items="serviceData.scopes"
                      item-text="title"
                      item-value="value"
                      label="Scope" />
                  </v-col>
                  <v-col cols="12" md="6">
                    <p v-if="service.scope">
                      Note: You need to generate and use API Keys to access this repository.
                      This repository will also have a cap limit for free usage after which you
                      will be charged or services discontinued.
                    </p>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
            <v-divider />
            <v-card-actions>
              <v-btn color="grey darken-1" @click="step = 1">
                previous
              </v-btn>
              <v-btn color="blue" @click="step = 3">
                continue
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-stepper-content>
        <v-stepper-content step="3">
          <v-card>
            <v-card-title>
              Deploy Service
            </v-card-title>
            <v-card-subtitle>
              Accept T&amp;C and create access token
            </v-card-subtitle>
            <v-card-text>
              <v-checkbox
                v-model="serviceData.legalNoticeAccepted"
                color="primary"
                label="I have read all the Terms and Conditions and accept them all *" />
            </v-card-text>
            <v-divider />
            <v-container>
              <span class="font-weight-bold">Note:</span> * marks required fields
            </v-container>
            <v-card-actions>
              <v-btn v-if="!token" color="grey darken-1" @click="step = 2">
                previous
              </v-btn>
              <v-row v-if="token">
                <v-col cols="12" md="8">
                  <v-text-field
                    class="ma-2"
                    v-model="token"
                    width="200"
                    outlined
                    append-outer-icon="mdi-clipboard-multiple"
                    @click:append-outer="TokenToClipboard"
                    label="Token" />
                </v-col>
                <v-col cols="12" md="4">
                  <v-btn block color="green" @click="Finish">
                    finish
                  </v-btn>
                </v-col>
              </v-row>
              <v-btn v-else color="green" :disabled="!ValidateSubmit()" @click="CreateService">
                create service
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
    <v-snackbar color="primary" top right v-model="snackbar.active" :timeout="3000">
      <v-container>
        <v-icon :color="snackbar.iconColor || 'blue'">
          {{ snackbar.icon || 'mdi-information' }}
        </v-icon>
        {{ snackbar.text }}
      </v-container>
    </v-snackbar>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  methods: {
    SetServiceIdentifier(fromName) {
      if (fromName) {
        this.serviceData.identifierSuffix = this.service.name.toLowerCase().replace(/\s/g, '-');
        this.service.identifier = `@${this.account.username}/${this.serviceData.identifierSuffix}`;
      } else {
        this.service.identifier = `@${this.account.username}/${this.serviceData.identifierSuffix}`;
      }
      this.CheckIdentifier();
    },
    async CheckIdentifier() {
      const response = await this.client.get(`http://localhost:8000/validate/service/identifier?q=${this.service.identifier}`);
      if (response.data) {
        if (response.data.available) {
          this.serviceData.identifierError = null;
        } else {
          this.serviceData.identifierError = 'Identifier not available';
        }
      } else {
        this.serviceData.identifierError = 'Error connecting to server';
      }
    },
    async CreateService() {
      if (this.serviceData.licenseSource === 'Text') {
        this.service.meta.license.url = null;
      } else {
        this.service.meta.license.text = null;
      }
      if (this.serviceData.descriptionSource === 'Text') {
        this.service.meta.description.url = null;
      } else {
        this.service.meta.description.text = null;
      }
      const response = await this.client.post('http://localhost:8000/services/add', this.service);
      if (response.data) {
        if (response.data.error) {
          this.Snack(response.data.error);
        } else {
          this.token = response.data.token;
        }
      }
    },
    ValidateSubmit() {
      return (
        this.service.name
        && this.service.identifier
        && !this.serviceData.identifierError
        && this.serviceData.legalNoticeAccepted
      );
    },
    TokenToClipboard() {
      this.$clipboard(this.token);
      this.Snack('Copied to Clipboard');
    },
    Snack(message) {
      this.snackbar.text = message;
      this.snackbar.active = true;
    },
    Finish() {
      this.$router.push({ path: '/dashboard' });
    },
  },
  data: () => ({
    step: 1,
    client: null,
    account: null,
    token: null,
    rules: {
      name: (v) => /^[A-Za-z][A-Za-z0-9\s]*$/g.test(v) || 'Invalid name',
      identifier: (v) => /^[a-z][a-z0-9-]*$/g.test(v) || 'Invalid ID',
    },
    serviceData: {
      identifierSuffix: null,
      identifierError: null,
      descriptionSource: 'Text',
      licenseSource: 'URL',
      licenseTypes: [
        'GNU AGPLv3',
        'GNU GPLv3',
        'GNU LGPLv3',
        'Mozilla Public License 2.0',
        'Apache License 2.0',
        'MIT License',
        'Boost Software License 1.0',
        'The Unlicense',
      ],
      scopes: [
        { title: 'Public', value: 0 },
        { title: 'Private', value: 1 },
      ],
      repoProviders: [
        { title: 'Github', icon: 'mdi-github', value: 'github' },
        { title: 'GitLab', icon: 'mdi-gitlab', value: 'gitlab' },
        { title: 'BitBucket', icon: 'mdi-bitbucket', value: 'bitbucket' },
        { title: 'SourceForge', icon: 'mdi-code-tags', value: 'sourceforge' },
        { title: 'AWS CodeCommit', icon: 'mdi-code-brackets', value: 'codecommit' },
      ],
      legalNoticeAccepted: false,
    },
    service: {
      identifier: null,
      name: null,
      repository: {
        provider: 'github',
        url: null,
      },
      meta: {
        description: {
          text: null,
          url: null,
        },
        license: {
          type: 'MIT License',
          url: null,
          text: null,
        },
      },
      testing: {
        enabled: false,
        procedure: null,
      },
      procedures: [],
      scope: 0, // 0 - public, 1 - private
    },
    snackbar: {
      active: false,
      text: null,
    },
  }),
  mounted() {
    this.client = axios.create({
      headers: {
        authorization: this.$store.state.token,
      },
      responseType: 'json',
    });
    this.client.get('http://localhost:8000/me').then((response) => {
      this.account = response.data;
    });
  },
};
</script>
