<template>
  <v-card v-if="service" class="pa-2">
    <v-card-title>
      EDIT SERVICE
      <v-spacer />
      <v-btn @click="Close" icon>
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-card-title>
    <v-card-subtitle>
      {{ service.name }}
    </v-card-subtitle>
    <v-card-text>
      <v-stepper non-linear class="mt-4">
        <v-stepper-header>
          <v-stepper-step editable color="green" :complete="step > 1" step="1">
            Service Details
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step editable color="green" :complete="step > 2" step="2">
            Additional Settings
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step editable color="green" step="3">
            Legal Forms
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step editable color="green" step="4">
            API Access
          </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card>
              <v-card-title>
                Basic Service Information
              </v-card-title>
              <v-card-subtitle>
                This information is visible to anyone who can connect to the service
              </v-card-subtitle>
              <v-card-text>
                <v-form>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-text-field v-model="service.name"
                        autocomplete="off"
                        @input="SetServiceIdentifier(true)"
                        outlined
                        :rules="[rules.name]"
                        label="Service Name*"
                        required />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-text-field
                        :value="serviceData.identifierSuffix"
                        disabled
                        outlined
                        v-if="account"
                        :rules="[rules.identifier]"
                        :error-messages="serviceData.identifierError"
                        label="Service ID*"
                        required >
                        <template v-slot:prepend>
                          <span class="white--text">@{{ account.username }}</span>
                          <v-icon>mdi-slash-forward</v-icon>
                        </template>
                      </v-text-field>
                    </v-col>
                    <v-col cols="6" md="2">
                      <v-select
                        v-model="service.repository.provider"
                        :items="serviceData.repoProviders"
                        item-text="title"
                        item-value="value"
                        :append-outer-icon="serviceData.repoProviders.find(
                          v => v.value === service.repository.provider
                        ).icon || 'mdi-code-braces'"
                        label="Provider"
                        outlined />
                    </v-col>
                    <v-col cols="6" md="4">
                      <v-text-field v-model="service.repository.url"
                        outlined
                        label="Repository URL" />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-col cols="12">
                        <v-select
                          v-model="serviceData.descriptionSource"
                          :items="['Text','URL']"
                          label="Description Source" />
                      </v-col>
                      <v-col v-if="serviceData.descriptionSource === 'Text'" cols="12">
                        <v-textarea
                          outlined
                          height="250"
                          label="Description"
                          v-model="service.meta.description.text" />
                      </v-col>
                      <v-col v-if="serviceData.descriptionSource === 'URL'" cols="12">
                        <v-text-field
                          outlined
                          v-model="service.meta.description.url"
                          label="Readme URL" />
                      </v-col>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-col cols="12">
                        <v-select
                          v-model="service.meta.license.type"
                          :items="serviceData.licenseTypes"
                          label="Select License" />
                      </v-col>
                      <v-col cols="12">
                        <v-select
                          v-model="serviceData.licenseSource"
                          :items="['Text','URL']"
                          label="License Source" />
                      </v-col>
                      <v-col v-if="serviceData.licenseSource === 'Text'" cols="12">
                        <v-textarea
                          outlined
                          label="License Text"
                          v-model="service.meta.license.text" />
                      </v-col>
                      <v-col v-if="serviceData.licenseSource === 'URL'" cols="12">
                        <v-text-field
                          outlined
                          v-model="service.meta.license.url"
                          label="License URL" />
                      </v-col>
                    </v-col>
                  </v-row>
                </v-form>
              </v-card-text>
              <v-divider />
            </v-card>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-card>
              <v-card-title>
                Service Settings
              </v-card-title>
              <v-card-subtitle>
                Customize the service features/settings as per your requirement.
              </v-card-subtitle>
              <v-card-text>
                <v-form>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-checkbox
                        v-model="service.testing.enabled"
                        color="primary"
                        label="Enable Testing" />
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field
                        outlined
                        v-if="service.testing.enabled"
                        v-model="service.testing.procedure"
                        label="Test Procedure" />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-select
                        outlined
                        disabled
                        v-model="service.scope"
                        :items="serviceData.scopes"
                        item-text="title"
                        item-value="value"
                        label="Scope" />
                    </v-col>
                    <v-col cols="12" md="6">
                      <p v-if="service.scope">
                        Note: You need to generate and use API Keys to access this repository.
                        This repository will also have a cap limit for free usage after which you
                        will be charged or services discontinued.
                      </p>
                    </v-col>
                  </v-row>
                </v-form>
              </v-card-text>
              <v-divider />
            </v-card>
          </v-stepper-content>
          <v-stepper-content step="3">
            <v-card>
              <v-card-title>
                Legal Information
              </v-card-title>
              <v-card-subtitle>
                Accept T&amp;C
              </v-card-subtitle>
              <v-card-text>
                <v-checkbox
                  v-model="serviceData.legalNoticeAccepted"
                  color="primary"
                  label="I have read all the Terms and Conditions and accept them all *" />
              </v-card-text>
              <v-divider />
              <v-container>
                <span class="font-weight-bold">Note:</span> * marks required fields
              </v-container>
            </v-card>
          </v-stepper-content>
          <v-stepper-content step="4">
            <v-card>
              <v-card-title>
                API Access
              </v-card-title>
              <v-card-subtitle>
                Generate access tokens and API keys
              </v-card-subtitle>
              <v-card-text>
                <v-row>
                  <v-col cols="12">
                    <h1 class="display-1">
                      Access Token
                    </h1>
                    <p>
                      <b>Note: </b> Liability of usage for an access token befalls upon the user.
                      An access tokens, once generated, can be used by anyone (either the intended
                      user or an external entity in possession of the key).
                      Keep these tokens secret.
                    </p>
                    <p>
                      <v-text-field
                        class="ma-2"
                        v-if="token"
                        :value="token"
                        max-width="200"
                        outlined
                        append-outer-icon="mdi-clipboard-multiple"
                        @click:append-outer="ToClipboard(token)"
                        label="Token" />
                    </p>
                    <p>
                      <v-btn
                        outlined
                        @click="GenerateToken">
                        generate token
                      </v-btn>
                    </p>
                  </v-col>
                  <v-col cols="12">
                    <h1 class="display-1">
                      API Keys
                    </h1>
                    <p>
                      Generate keys that can be used by the client
                      instances to access your Service API
                      <b>(Auto-saved)</b>
                    </p>
                    <p v-if="!service.scope">
                      <b>Note:</b> Public APIs (such as this one) do not
                      necessarily require an API Key.
                    </p>
                    <p>
                      <v-list>
                        <v-list-item :key="index" v-for="(key, index) in service.apiKeys">
                          <v-list-item-avatar>
                            <v-btn @click="ToClipboard(key)" outlined color="blue" icon>
                              <v-icon>mdi-key</v-icon>
                            </v-btn>
                          </v-list-item-avatar>
                          <v-list-item-content>
                            {{ key }}
                          </v-list-item-content>
                          <v-list-item-action>
                            <v-btn @click="RemoveKey(key)" outlined color="red" icon>
                              <v-icon>mdi-delete</v-icon>
                            </v-btn>
                          </v-list-item-action>
                        </v-list-item>
                      </v-list>
                    </p>
                    <p>
                      <v-btn
                        outlined
                        @click="GenerateKey">
                        generate key
                      </v-btn>
                    </p>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card-text>
    <v-card-actions>
      <v-spacer />
      <v-btn @click="Save" color="green">
        save
      </v-btn>
    </v-card-actions>
    <v-snackbar color="primary" top right v-model="snackbar.active" :timeout="3000">
      <v-container>
        <v-icon :color="snackbar.iconColor || 'blue'">
          {{ snackbar.icon || 'mdi-information' }}
        </v-icon>
        {{ snackbar.text }}
      </v-container>
    </v-snackbar>
  </v-card>
</template>

<script>
import axios from 'axios';

export default {
  props: {
    value: Object,
  },
  methods: {
    GenerateKey() {
      this.client.post('http://localhost:8000/services/generate-key', {
        identifier: this.service.identifier,
      }).then((response) => {
        if (response.data.error) {
          this.$emit('error', response.data.error);
        } else {
          this.service.apiKeys.push(response.data.key);
          this.$forceUpdate();
          this.$emit('input', this.service);
        }
      });
    },
    GenerateToken() {
      this.client.post('http://localhost:8000/services/generate-token', {
        identifier: this.service.identifier,
      }).then((response) => {
        if (response.data.error) {
          this.$emit('error', response.data.error);
        } else {
          this.token = response.data.token;
        }
      });
    },
    RemoveKey(key) {
      this.client.post('http://localhost:8000/services/remove-key', {
        identifier: this.service.identifier,
        key,
      }).then((response) => {
        if (response.data.error) {
          this.$emit('error', response.data.error);
        } else {
          this.service.apiKeys = this.service.apiKeys.filter((v) => v !== key);
          this.$forceUpdate();
          this.$emit('input', this.service);
        }
      });
    },
    ToClipboard(text) {
      this.$clipboard(text);
      this.Snack('Copied to Clipboard');
    },
    Snack(message) {
      this.snackbar.text = message;
      this.snackbar.active = true;
    },
    Save() {
      if (this.serviceData.licenseSource === 'Text') {
        this.service.meta.license.url = null;
      } else {
        this.service.meta.license.text = null;
      }
      if (this.serviceData.descriptionSource === 'Text') {
        this.service.meta.description.url = null;
      } else {
        this.service.meta.description.text = null;
      }
      this.client.post('http://localhost:8000/services/update', {
        identifier: this.service.identifier,
        set: this.service,
      }).then((response) => {
        if (response.data.error) {
          this.$emit('error', response.data.error);
        } else {
          this.$emit('input', this.service);
        }
      }).finally(() => {
        this.Close();
      });
    },
    Close() {
      this.$emit('close');
    },
  },
  data: () => ({
    token: null,
    serviceData: {
      identifierSuffix: null,
      identifierError: null,
      descriptionSource: 'Text',
      licenseSource: 'URL',
      licenseTypes: [
        'GNU AGPLv3',
        'GNU GPLv3',
        'GNU LGPLv3',
        'Mozilla Public License 2.0',
        'Apache License 2.0',
        'MIT License',
        'Boost Software License 1.0',
        'The Unlicense',
      ],
      scopes: [
        { title: 'Public', value: 0 },
        { title: 'Private', value: 1 },
      ],
      repoProviders: [
        { title: 'Github', icon: 'mdi-github', value: 'github' },
        { title: 'GitLab', icon: 'mdi-gitlab', value: 'gitlab' },
        { title: 'BitBucket', icon: 'mdi-bitbucket', value: 'bitbucket' },
        { title: 'SourceForge', icon: 'mdi-code-tags', value: 'sourceforge' },
        { title: 'AWS CodeCommit', icon: 'mdi-code-brackets', value: 'codecommit' },
      ],
      legalNoticeAccepted: false,
    },
    serviceDefault: {
      identifier: null,
      name: null,
      repository: {
        provider: 'github',
        url: null,
      },
      meta: {
        description: {
          text: null,
          url: null,
        },
        license: {
          type: 'MIT License',
          url: null,
          text: null,
        },
      },
      testing: {
        enabled: false,
        procedure: null,
      },
      procedures: [],
      scope: 0, // 0 - public, 1 - private
    },
    service: null,
    step: 1,
    rules: {
      name: (v) => /^[A-Za-z][A-Za-z0-9\s]*$/g.test(v) || 'Invalid name',
      identifier: (v) => /^[a-z][a-z0-9-]*$/g.test(v) || 'Invalid ID',
    },
    account: null,
    snackbar: {
      active: false,
      text: null,
    },
  }),
  mounted() {
    this.client = axios.create({
      headers: {
        authorization: this.$store.state.token,
      },
      responseType: 'json',
    });
    this.client.get('http://localhost:8000/me').then((response) => {
      this.account = response.data;
    });
    this.service = Object.assign(this.serviceDefault, this.value);
    [, this.serviceData.identifierSuffix] = this.service.identifier.split('/');
    if (this.service.meta.description.text) {
      this.serviceData.descriptionSource = 'Text';
    } else {
      this.serviceData.descriptionSource = 'URL';
    }
    if (this.service.meta.license.text) {
      this.serviceData.licenseSource = 'Text';
    } else {
      this.serviceData.licenseSource = 'URL';
    }
    this.serviceData.legalNoticeAccepted = true;
  },
};
</script>
