<template>
  <v-container>
    <v-card flat>
      <v-card-text>
        <v-text-field v-model="name" autocomplete="off" label="Full Name" />
        <v-text-field v-model="username"
          @change="ValidateUsername"
          autocomplete="off"
          :append-icon="'mdi-' + usernameState"
          label="Username" />
        <v-text-field v-model="email" autocomplete="off" label="Email" />
        <v-text-field v-model="password"
          :append-icon="showPassword ? 'mdi-eye-off' : 'mdi-eye'"
          :type="showPassword ? 'text' : 'password'"
          @click:append="showPassword = !showPassword"
          label="Password" />
        <v-text-field v-model="confirmPassword"
          :append-icon="showPassword ? 'mdi-eye-off' : 'mdi-eye'"
          :type="showPassword ? 'text' : 'password'"
          @click:append="showPassword = !showPassword"
          label="Confirm Password" />
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn color="blue" @click="Register">
          <v-icon left>
            mdi-pen
          </v-icon>
          register
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  methods: {
    async ValidateUsername() {
      this.usernameState = 'dots-horizontal';
      const response = await axios.get(`http://localhost:8000/validate/user/username?q=${this.username}`);
      if (response.data.available) {
        this.usernameState = 'check';
      } else {
        this.usernameState = 'close';
      }
    },
    async Register() {
      const response = await axios.post('http://localhost:8000/register', {
        name: this.name,
        username: this.username,
        email: this.email,
        password: this.password,
      });
      if (response.data.token) {
        await this.$store.dispatch('setToken', response.data.token);
        this.$router.push({ path: '/dashboard' });
      }
    },
  },
  data: () => ({
    name: null,
    username: null,
    email: null,
    password: null,
    confirmPassword: null,
    showPassword: false,
    usernameState: 'check',
  }),
};
</script>
