import Vue from 'vue';
import VueRouter from 'vue-router';
import Landing from '../views/Landing.vue';
import Dashboard from '../views/Dashboard.vue';
import Console from '../components/dashboard/Console.vue';
import LaunchWizard from '../components/dashboard/LaunchWizard.vue';
import ServiceManager from '../components/dashboard/ServiceManager.vue';
import ClusterManager from '../components/dashboard/ClusterManager.vue';
import store from '../store';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Landing',
    component: Landing,
    meta: {
      title: 'Marvin - WebRTC powered IoT',
    },
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard,
    children: [
      {
        path: '',
        component: Console,
      },
      {
        path: 'service-launch-wiz',
        component: LaunchWizard,
      },
      {
        path: 'service-manager',
        component: ServiceManager,
      },
      {
        path: 'clusters',
        component: ClusterManager,
      },
    ],
    meta: {
      title: 'Marvin - Dashboard',
      requiresAuth: true,
    },
  },
  {
    path: '/about',
    name: 'About',
    meta: {
      title: 'Marvin - About',
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/About.vue'),
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    if (!store.state.token) {
      next({ name: 'Landing' });
    } else {
      next(); // go to wherever I'm going
    }
  } else {
    next(); // does not require auth, make sure to always call next()!
  }
});

// This callback runs before every route change, including on page load.
router.beforeEach((to, from, next) => {
  // This goes through the matched routes from
  // last to first, finding the closest route with a title.
  // eg. if we have /some/deep/nested/route
  // and /some, /deep, and /nested have titles,
  // nested's will be chosen.
  const nearestWithTitle = to.matched.slice()
    .reverse()
    .find((r) => r.meta && r.meta.title);

  // Find the nearest route element with meta tags.
  const nearestWithMeta = to.matched.slice()
    .reverse()
    .find((r) => r.meta && r.meta.metaTags);
  /* const previousNearestWithMeta = from.matched.slice()
                                      .reverse()
                                      .find((r) => r.meta && r.meta.metaTags); */

  // If a route with a title was found, set the document (page) title to that value.
  if (nearestWithTitle) document.title = nearestWithTitle.meta.title;

  // Remove any stale meta tags from the document using the key attribute we set below.
  Array.from(document.querySelectorAll('[data-vue-router-controlled]'))
    .map((el) => el.parentNode.removeChild(el));

  // Skip rendering meta tags if there are none.
  if (!nearestWithMeta) return next();

  // Turn the meta tag definitions into actual elements in the head.
  nearestWithMeta.meta.metaTags.map((tagDef) => {
    const tag = document.createElement('meta');

    Object.keys(tagDef).forEach((key) => {
      tag.setAttribute(key, tagDef[key]);
    });

    // We use this to track which meta tags we create, so we don't interfere with other ones.
    tag.setAttribute('data-vue-router-controlled', '');

    return tag;
  })
  // Add the meta tags to the document head.
    .forEach((tag) => document.head.appendChild(tag));

  next();
  return 0;
});

export default router;
