<template>
  <v-container class="pa-2" fluid>
    <h1 class="display-1 font-weight-thin mb-4">
      Manage Services
    </h1>
    <v-divider />
    <v-row>
      <v-col cols="12" md="6">
        <v-card>
          <v-card-text>
            <v-data-iterator
              :items="services"
              :items-per-page.sync="table.servicesPerPage"
              :page="table.page"
              :search="table.search"
              hide-default-footer>
              <template v-slot:header>
                <v-toolbar
                  dark
                  color="primary darken-1"
                  class="mb-1">
                  <v-text-field
                    v-model="table.search"
                    clearable
                    flat
                    solo-inverted
                    hide-details
                    prepend-inner-icon="mdi-magnify"
                    label="Search" />
                </v-toolbar>
              </template>
              <template v-slot:default="{ items }">
                <v-list>
                  <v-list-item-group
                    v-model="table.selected"
                    mandatory
                    color="blue">
                    <v-list-item
                      shaped
                      v-for="(item, i) in items"
                      :key="i">
                      <v-list-item-title class="white--text">
                        {{ item.name }}
                      </v-list-item-title>
                      <v-list-item-action>
                        <v-btn @click="OpenEditor(item)" color="green" icon>
                          <v-icon>
                            mdi-cogs
                          </v-icon>
                        </v-btn>
                      </v-list-item-action>
                      <v-list-item-action>
                        <v-btn @click="Dialog({
                          title: 'Are you sure?',
                          text: 'This action is permanent and cannot be undone',
                          acceptText: 'delete',
                          callback: () => Delete(item.identifier),
                        })" color="red" icon>
                          <v-icon>
                            mdi-delete
                          </v-icon>
                        </v-btn>
                      </v-list-item-action>
                    </v-list-item>
                  </v-list-item-group>
                </v-list>
              </template>
              <template v-slot:footer>
                <v-row class="mt-2" align="center" justify="center">
                  <v-spacer></v-spacer>
                  <span
                    class="mr-4">
                    Page {{ table.page }} of {{ table.npages }}
                  </span>
                  <v-btn
                    fab
                    dark
                    color="blue darken-3"
                    class="mr-1"
                    @click="NextPage">
                    <v-icon>mdi-chevron-left</v-icon>
                  </v-btn>
                  <v-btn
                    fab
                    dark
                    color="blue darken-3"
                    class="ml-1"
                    @click="PreviousPage">
                    <v-icon>mdi-chevron-right</v-icon>
                  </v-btn>
                </v-row>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col v-if="services.length" cols="12" md="6">
        <v-card>
          <v-card-title>
            Overview
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="12">
                <v-icon :color="
                  services[table.selected].scope ?
                  'blue' : 'green'
                " left>{{
                  services[table.selected].scope ?
                  'mdi-lock' : 'mdi-lock-open'
                }}</v-icon>
                {{
                  services[table.selected].scope ?
                  'PRIVATE' : 'PUBLIC'
                }}
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-row>
                  <v-col cols="12">
                    <v-icon left> mdi-book-lock </v-icon>
                    {{ services[table.selected].meta.license.type }}
                    <template v-if="services[table.selected].meta.license.url">
                      <v-btn
                        @click="OpenExternal(services[table.selected].meta.license.url)"
                        outlined>
                        open license
                        <v-icon right>
                          mdi-open-in-new
                        </v-icon>
                      </v-btn>
                    </template>
                    <template v-else>
                      <v-btn
                        v-if="services[table.selected].meta.license.text"
                        @click="Dialog({
                          title: 'License',
                          text: services[table.selected].meta.license.text,
                          acceptText: 'okay',
                          cancelText: null,
                          maxWidth: 800,
                          persistent: true,
                          callback: () => {},
                        })"
                        outlined>
                        open license
                        <v-icon right>
                          mdi-open-in-new
                        </v-icon>
                      </v-btn>
                    </template>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-icon color="yellow" left> mdi-network </v-icon>
                USAGE: {{ ByteFriendly(services[table.selected].usage) }}
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-icon left> mdi-book-open </v-icon>
                <template v-if="services[table.selected].meta.description.url">
                  <v-btn
                    @click="OpenExternal(services[table.selected].meta.description.url)"
                    outlined>
                    open description
                    <v-icon right>
                      mdi-open-in-new
                    </v-icon>
                  </v-btn>
                </template>
                <template v-else>
                  <v-btn
                    v-if="services[table.selected].meta.description.text"
                    @click="Dialog({
                      title: 'Description',
                      text: services[table.selected].meta.description.text,
                      acceptText: 'okay',
                      cancelText: null,
                      maxWidth: 400,
                      persistent: true,
                      callback: () => {},
                    })"
                    outlined>
                    open description
                    <v-icon right>
                      mdi-open-in-new
                    </v-icon>
                  </v-btn>
                  <span v-else>
                    No description
                  </span>
                </template>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar color="primary" top right v-model="snackbar.active" :timeout="3000">
      <v-container>
        <v-icon :color="snackbar.iconColor || 'blue'">
          {{ snackbar.icon || 'mdi-information' }}
        </v-icon>
        {{ snackbar.text }}
      </v-container>
    </v-snackbar>
    <v-dialog
    v-model="dialog.active"
    :persistent="dialog.persistent"
    :max-width="dialog.maxWidth">
      <v-card>
        <v-card-title>
          {{ dialog.title }}
        </v-card-title>
        <v-card-text>
          <v-textarea
          disabled
          auto-grow
          :value="dialog.text"/>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            v-if="dialog.cancelText"
            @click="dialog.active = false">
            {{ dialog.cancelText }}
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            v-if="dialog.acceptText"
            @click="dialog.call">
            {{ dialog.acceptText }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="editor.active"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition">
      <service-editor v-model="services[table.selected]" @close="editor.active = false" />
    </v-dialog>
  </v-container>
</template>

<script>
import axios from 'axios';
import ServiceEditor from './ServiceEditor.vue';

export default {
  components: {
    ServiceEditor,
  },
  methods: {
    Update() {
      this.client.post('http://localhost:8000/services/get').then((response) => {
        if (response.data) {
          if (response.data.error) {
            this.Snack(`Error: ${response.data.error}`, 'mdi-alert');
          } else {
            this.services = response.data.services;
            this.table.selected = 0;
            this.table.npages = Math.ceil(this.services.length / this.table.servicesPerPage);
          }
        } else {
          this.Snack('Error connecting to server!', 'mdi-alert');
        }
      });
    },
    NextPage() {
      if (this.table.page + 1 <= this.table.npages) {
        this.table.page += 1;
      }
    },
    PreviousPage() {
      if (this.table.page - 1 >= this.table.npages) {
        this.table.page -= 1;
      }
    },
    Snack(message, icon = null) {
      this.snackbar.icon = icon;
      this.snackbar.text = message;
      this.snackbar.active = true;
    },
    Delete(identifier) {
      this.client.post('http://localhost:8000/services/remove', {
        identifier,
      }).then((response) => {
        if (response.data) {
          if (response.data.error) {
            this.Snack(`Error: ${response.data.error}`, 'mdi-alert');
          } else {
            this.Snack('Successfully removed');
            this.services = this.services.filter((v) => v.identifier === identifier);
          }
        } else {
          this.Snack('Error connecting to server!', 'mdi-alert');
        }
      });
    },
    Dialog(config) {
      this.dialog = Object.assign(this.dialogDefaults, config);
      this.dialog.call = () => {
        this.dialog.callback();
        this.dialog.active = false;
      };
      this.dialog.active = true;
    },
    ByteFriendly(number) {
      if (number < 1024) {
        return `${number} bytes`;
      }
      if (number < 1024 * 1024) {
        return `${Math.floor(number / 1024)} KB`;
      }
      if (number < 1024 * 1024 * 1024) {
        return `${Math.floor(number / (1024 * 1024))} MB`;
      }
      return `${Math.floor(number / (1024 * 1024 * 1024))} GB`;
    },
    OpenExternal(link) {
      window.open(link, '_blank');
    },
    OpenEditor(service) {
      this.editor.service = service;
      this.editor.active = true;
    },
  },
  data: () => ({
    table: {
      selected: 0,
      page: 1,
      servicesPerPage: 10,
      npages: 1,
      search: '',
    },
    services: [],
    account: null,
    client: null,
    snackbar: {
      icon: null,
      active: false,
      text: null,
    },
    dialogDefaults: {
      active: false,
      persistent: false,
      title: null,
      text: null,
      cancelText: 'cancel',
      acceptText: 'accept',
      call: () => {
        this.dialog.active = false;
      },
      callback: null,
    },
    dialog: {
      active: false,
      persistent: false,
      title: null,
      text: null,
      cancelText: 'cancel',
      acceptText: 'accept',
      call: () => {
        this.dialog.active = false;
      },
      maxWidth: 300,
      callback: null,
    },
    editor: {
      active: false,
      service: {},
    },
  }),
  mounted() {
    this.client = axios.create({
      headers: {
        authorization: this.$store.state.token,
      },
      responseType: 'json',
    });
    this.client.get('http://localhost:8000/me').then((response) => {
      this.account = response.data;
    });
    this.Update();
  },
};
</script>
